<template>
  <header>
		<div class="header-area">
			<div class="container">
				<div class="row">
					<div class="col-md-3 col-sm-3">
                        <div class="logo"> <!-- Start Logo Area -->
                            <a href="#">
                            	<p>Agung</p>
							</a>
                        </div> <!-- End Logo Area -->
                    </div>
					 <div class="col-md-9 col-sm-9">
                        <div class="main-menu"> <!-- Start Menu Area -->
                            <div class="navbar">
                                <div class="navbar-header">
                                    <button type="button" class="navbar-toggle" data-toggle="collapse" data-target=".navbar-collapse">
                                        <span class="sr-only">Toggle navigation</span>
                                        <span class="icon-bar"></span>
                                        <span class="icon-bar"></span>
                                        <span class="icon-bar"></span>
                                    </button>
                                </div>
                                <div class="navbar-collapse collapse">
                                    <ul class="nav navbar-nav navbar-right">
                                        <li><a class="smoth-scroll" href="#home">Home <div class="ripple-wrapper"></div></a></li>
                                        <li><a class="smoth-scroll" href="#about">About</a></li>
                                        <li><a class="smoth-scroll" href="#service">Service</a></li>
                                        <li><a class="smoth-scroll" href="#portfolio">Portfolio</a></li>
                                        <li><a class="smoth-scroll" href="#contact">Contact</a></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
				</div>
			</div>
		</div>
	</header>
</template>

<script>
export default {
  methods: {
    handleScroll (event) {
      let header = document.querySelector("header");
      if (window.scrollY > 100 && !header.className.includes('sticky')) {
        header.classList.add('sticky');
      } else if (window.scrollY < 100) {
        header.classList.remove('sticky');
      }
    }
  },
  created () {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  }
}
</script>

<style lang="css">
</style>



